import { PureComponent, ReactElement, ChangeEvent } from 'react';
import { connect } from 'react-redux';
import { Row, Col } from 'reactstrap';
import DataTable, { createTheme } from 'react-data-table-component';
import { CHUNK_DATA_DELAY } from '../../config/appConfig';
import { TStoreState } from '../../store/config/store';
import loadingService from '../../service/loadingService';
import { getTokenInfos } from '../../service/tokenInfoService';
import { getPools } from '../../service/tokenPoolService';
import { updateTokenPrice } from '../../service/tokenPriceService';
import { checkBrowserNotification } from '../../service/notificationService';
import { notifyVariationPrice } from '../../service/notificationService';
import {
  mappingTokenTable,
  sortTopMoverTokens,
} from '../../service/tokenMappingService';
import {
  mappingVariationBrowserNotification,
  // mappingVariationBrowserNotificationFomo,
} from '../../service/notificationMappingService';
import { ITokenTable } from '../../model/tokenModel';
import { IBrowserNotification } from '../../model/notificationModel';
import CoinTitle from './CoinTitle/CoinTitle';
import Variation from './Variation/Variation';
import Price from './Price/Price';
import Pool from './Pool/Pool';
// import style from './TokenTable.module.scss';

interface IStoreProps {
  tokens: ITokenTable[];
  tokenNotifications: IBrowserNotification[];
  // tokenNotificationsFomo: IBrowserNotification[];
}
interface IHOCProps {}
interface IOwnProps {}
interface IOwnState {
  filterText: string;
}

class TokenTable extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  private columnConfigs = [
    {
      name: 'Coin',
      cell: (row: ITokenTable): ReactElement => {
        return (
          <CoinTitle
            coinTitle={row.coinTitle}
            address={row.address}
            logoURI={row.logoURI}
            web={row.web}
          />
        );
      },
      minWidth: '400px',
    },
    {
      name: 'Variation',
      cell: (row: ITokenTable): ReactElement => {
        return <Variation variation={row.variation} division={row.division} />;
      },
      minWidth: '80px',
    },
    {
      name: 'Price',
      cell: (row: ITokenTable): ReactElement => {
        return <Price price={row.price} />;
      },
      minWidth: '100px',
    },
    {
      name: 'Pool',
      // selector: (row: ITokenTable): string => row.pool,
      cell: (row: ITokenTable): ReactElement => {
        return <Pool pool={row.pool} />;
      },
      minWidth: '120px',
    },
  ];

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      filterText: '',
    };

    createTheme('dark', {
      background: {
        default: 'transparent',
      },
    });

    this.onChangeFilter = this.onChangeFilter.bind(this);
  }

  async componentDidMount() {
    loadingService.showLoading();
    await getTokenInfos();
    await getPools();
    loadingService.hideLoading();

    this.recursionUpdateTokenPrice();

    checkBrowserNotification();
  }

  componentDidUpdate(prevProps: IStoreProps) {
    if (
      JSON.stringify(this.props.tokenNotifications) !==
      JSON.stringify(prevProps.tokenNotifications)
    ) {
      notifyVariationPrice(this.props.tokenNotifications);
    }

    // if (
    //   JSON.stringify(this.props.tokenNotificationsFomo) !==
    //   JSON.stringify(prevProps.tokenNotificationsFomo)
    // ) {
    //   notifyVariationPrice(this.props.tokenNotificationsFomo);
    // }
  }

  private async recursionUpdateTokenPrice() {
    await updateTokenPrice();
    await new Promise((r) => setTimeout(r, CHUNK_DATA_DELAY));
    await this.recursionUpdateTokenPrice();
  }

  private onChangeFilter(e: ChangeEvent<HTMLInputElement>) {
    this.setState({
      filterText: e.target.value,
    });
  }

  private filterTokens(tokens: ITokenTable[]): ITokenTable[] {
    const { filterText } = this.state;

    if (!filterText) {
      const topMoverTokens: ITokenTable[] = sortTopMoverTokens(tokens);

      return topMoverTokens;
    }

    const filteredTokens: ITokenTable[] = tokens.filter(
      (token: ITokenTable): boolean => {
        return (
          token.coinTitle.toLowerCase().includes(filterText.toLowerCase()) ||
          token.address.toLowerCase() === filterText.toLowerCase()
        );
      }
    );

    return filteredTokens;
  }

  render(): ReactElement {
    return (
      <Row>
        <Col
          xl={{
            offset: 1,
            size: 10,
          }}
        >
          <div className='fk-container px-2 px-lg-4 px-xl-5 pt-2 pt-lg-4 pt-xl-5 pb-xl-4'>
            <input
              placeholder='Enter token name / address...'
              className={'mb-1 fk-input fk-input-lg'}
              onChange={this.onChangeFilter}
            />
            <DataTable
              columns={this.columnConfigs}
              data={this.filterTokens(this.props.tokens)}
              pagination
              responsive
              theme={'dark'}
              customStyles={{
                rows: {
                  style: {
                    minHeight: '52px',
                  },
                },
                headCells: {
                  style: {
                    fontSize: '16px',
                    fontWeight: '700',
                  },
                },
                cells: {
                  style: {
                    fontSize: '14px',
                    fontWeight: '500',
                  },
                },
                pagination: {
                  style: {
                    fontSize: '14px',
                    fontWeight: '500',
                  },
                },
              }}
            />
          </div>
        </Col>
      </Row>
    );
  }
}

const mapState = (state: TStoreState): IStoreProps => ({
  tokens: mappingTokenTable(state.tokens),
  tokenNotifications: mappingVariationBrowserNotification(state.tokens),
  // tokenNotificationsFomo: mappingVariationBrowserNotificationFomo(state.tokens),
});

export default connect(mapState)(TokenTable);
